/*
==========================
GENERAL
==========================
*/
.SearchBox {
  resize: none; }

textarea {
  resize: vertical;
  max-height: 300px;
  min-height: 40px;
  color: #959595; }

body {
  font-family: "Helvetica Neue", Helvetica, "Varela Round", sans-serif; }

h1, h2, h3, h4, h5 {
  color: #959595; }

/*
==========================
HEADER
==========================
*/
.Bookmarks-Header {
  background-color: #ffea6c;
  top: 0px;
  right: 0px;
  width: 300px;
  position: fixed;
  height: 140px; }

.Bookmarks-Body {
  background: white;
  top: 140px;
  right: 0px;
  width: 300px;
  position: fixed;
  height: 100%; }

.Bookmarks-Title-Text {
  width: 260px;
  height: 40px;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1.6px;
  text-align: left;
  color: #ffffff;
  padding-left: 20px;
  padding-top: 80px; }

/*
==========================
NOTES
==========================
*/
.Bookmarks {
  border: solid 0.2px #959595;
  color: #959595; }

/*
==========================
SEARCH
==========================
*/
.SearchBox {
  right: 20px;
  top: 160px;
  width: 260px;
  height: 40px;
  border-radius: 10px;
  -webkit-box-shadow: 2px 2px 4px 0 #959595;
          box-shadow: 2px 2px 4px 0 #959595;
  border: solid 0.3px #959595;
  border: solid 0.3px var(--warm-grey);
  -ms-flex-align: center;
      align-items: center;
  color: #959595; }

.SearchBoxText {
  padding-top: 15px;
  font-size: 12px;
  letter-spacing: 2px;
  text-align: center; }

/*
==========================
BUTTONS
==========================
*/
.bookmarksExitButton {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  top: 20px;
  right: 20px;
  color: #ffea6c;
  position: fixed;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffffff), to(#f6f6f6));
  background: -webkit-linear-gradient(top, #ffffff 5%, #f6f6f6 100%);
  background: -o-linear-gradient(top, #ffffff 5%, #f6f6f6 100%);
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  border-radius: 50%;
  display: inline-block;
  font-size: 20px;
  font-weight: 700;
  font-style: bold;
  text-align: center;
  border: none;
  background-color: #ffffff; }
  .bookmarksExitButton:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, #f3f3f3), to(#ffffff));
    background: -webkit-linear-gradient(top, #f3f3f3 20%, #ffffff 100%);
    background: -o-linear-gradient(top, #f3f3f3 20%, #ffffff 100%);
    background: linear-gradient(to bottom, #f3f3f3 20%, #ffffff 100%);
    background-color: #f6f6f6; }



