
body {
    font-family: "Helvetica Neue", Helvetica, 'Varela Round', sans-serif;
}

/*  main blue header */ 
.Todos-Header {
    background-color: #00a7ff;
    top: 0px;
    right: 0px;
    width: 300px;
    position: fixed;
    height: 140px
}

/* exit button up right in the header */
.exitButton {
    border-radius: 50%;
    width: 35px;
    height: 35px;
    top: 20px;
    right: 20px;
    position: fixed;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffffff), to(#f6f6f6));
    background: -webkit-linear-gradient(top, #ffffff 5%, #f6f6f6 100%);
    background: -o-linear-gradient(top, #ffffff 5%, #f6f6f6 100%);
    background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
    border-radius:50%;
    display:inline-block;
    cursor:pointer;
    color:#00a7ff;
    font-size:20px;
    font-weight: 700;
    font-style: bold;
    text-align: center;
    border: none;
    background-color:#ffffff;
}
.exitButton:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #f6f6f6), to(#ffffff));
    background: -webkit-linear-gradient(top, #f6f6f6 5%, #ffffff 100%);
    background: -o-linear-gradient(top, #f6f6f6 5%, #ffffff 100%);
    background: linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
    background-color:#f6f6f6;
}


